import { Pipe, PipeTransform } from '@angular/core';
import { Client } from '../client.model';


@Pipe({
  name: 'filter'
})
export class FilterPipe implements PipeTransform {

  transform(clients: Client[], search: string): Client[] | null {
    if (!search.length) {
      return clients;
    } else {
      return clients.filter( c => (c.firstName + c.lastName + c.companyName).toLocaleLowerCase().includes(search.toLocaleLowerCase()) );
    }
  }

}


