import { Component, OnInit, ResolvedReflectiveFactory } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ClientService } from '../../shared/services/client.service';
import { ActivatedRoute, Params } from '@angular/router';
import { Client } from '../../shared/client.model';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { User } from 'src/app/shared/models/user.model';
import { UserService } from 'src/app/shared/services/user.service';
import { AlertPromise } from 'selenium-webdriver';


@Component({
  selector: 'app-client-edit',
  templateUrl: './client-edit.component.html',
  styleUrls: ['./client-edit.component.css']
})
export class ClientEditComponent implements OnInit {
  public clientForm: FormGroup;
  public client: Client;
  private edit: boolean;
  private paramsID: any;
  color = 'warn';
  checked = null;
  public currentUser;
  public fileToUpload: File = null;
  public imgExtension: string[] = [
    'image/webp', 
    'image/tiff', 
    'image/svg+xml', 
    'image/png', 
    'image/jpeg', 
    'image/x-icon', 
    'image/gif'
  ];


  public companyTypes: string[] = [
    'BET',
    'Archi',
    'Régie',
    'Economiste',
    'Domaine Viticole',
    'Régie / Gestion'
  ];

  public reponses: string[] = [
    'OUI',
    'NON'
  ];

  public civilites: string[] = [
    'M',
    'Mme'
  ];

  constructor(private userService: UserService, private router: Router, private activatedRoute: ActivatedRoute, private fb: FormBuilder, private clientService: ClientService) { }

  ngOnInit() {
    this.currentUser = this.userService.currentUser;
    
    this.activatedRoute.params.subscribe(( params: Params ) => {
      if (params.id) {
        this.paramsID = params.id;
        this.edit = true;
        this.clientService.getClient(this.paramsID).subscribe ( (client: Client) => {
          this.client = client
          this.initForm(this.client);
          this.clientDeQui();
        });
      }else{
        this.initForm();
        this.edit = false;
      }
    });
  }

  clientDeQui(){
    const array = this.client.customerOf;
    if(array.includes(this.currentUser.value.companyName)){
      this.checked = true;

    } else {
      this.checked = false;
    }
  }

  initForm(client:Client = {
    selection: false,
    customerOf: [],
    civilite: '',
    lastName: '',
    firstName: '',
    post: '',
    companyName: '',
    companyAdress: '',
    companyPostCode: '',
    companyCity: '',
    companyType: '',
    cellPhone: '',
    phone: '',
    mail: '',
    companyLogo: 'defaultLogo.png',
    infos: 'Hello',
    visitesSBTX: [],
  }){
    this.clientForm = this.fb.group({
      selection: [client.selection],
      customerOf: [client.customerOf],
      civilite: [client.civilite, Validators.required],
      lastName: [client.lastName, Validators.required],
      firstName: [client.firstName],
      post: [client.post, Validators.required],
      companyName: [client.companyName, Validators.required],
      companyAdress: [client.companyAdress, Validators.required],
      companyPostCode: [client.companyPostCode, Validators.required],
      companyCity: [client.companyCity, Validators.required],
      companyType: [client.companyType, Validators.required],
      cellPhone: [client.cellPhone],
      phone: [client.phone, Validators.required],
      mail: [client.mail, Validators.required],
      companyLogo: [client.companyLogo],
      infos: [client.infos],
      visitesSBTX: [client.visitesSBTX],
      _id: [client._id]
    })
  }

  createClient(){
    if(this.edit) {
      this.clientService.editClient(this.clientForm.value, this.client._id);
      this.router.navigate(['ficheClient/' + this.paramsID.toString()]);
    } else {
      this.clientService.addClient(this.clientForm.value);
      // this.router.navigate(['listingClients']);
    }
  }

  editArrowBack(){
    if (this.edit){
      this.router.navigate(['ficheClient/' + this.paramsID.toString()]);
    }else{
      this.router.navigate(['listingClients']);
    }
  }

  postNewAvatar(files: FileList){
    if (this.imgExtension.includes(files.item(0).type)){
      this.fileToUpload = files.item(0);
      this.uploadFileToActivity()
    } else {
      alert('extensions acceptées : webp, tiff, svg+xml, png, jpeg, gif');
    }
  }

  uploadFileToActivity() {
    this.clientService.postAvatar(this.fileToUpload, this.paramsID, this.client);
  }

  changeCustomerOf(value){
    // if(value === true){
    //  this.checked = true;
    // }else{
    //  this.checked = false;
    // };
    const currentUserCompanyName = this.currentUser.value.companyName;
    this.clientService.customerOf(value.checked, this.client, currentUserCompanyName);
  }

}
