import { Component, OnInit } from '@angular/core';
import { Client } from '../../shared/client.model';
import { ClientService } from '../../shared/services/client.service';
import { User } from 'src/app/shared/models/user.model';
import { UserService } from 'src/app/shared/services/user.service';
import { Observable } from 'rxjs';
import { FilterPipe } from '../../shared/pipes/filter.pipe';
import * as moment from 'moment';
import { filter } from 'rxjs/operators';



@Component({
  selector: 'app-clients-list',
  templateUrl: './clients-list.component.html',
  styleUrls: ['./clients-list.component.css'],
  providers: [FilterPipe]
})
export class ClientsListComponent implements OnInit {
  search: string = '';
  public clients: Client[];
  public currentUser: Observable<User>;

  constructor(private clientService : ClientService, private userService: UserService) { }

  ngOnInit() {
    this.userService.getCurrentUser().subscribe();
    this.currentUser = this.userService.currentUser;

    this.clientService.clients.subscribe( (clients: Client[]) => {
      this.clients = clients;
    });
    console.log(this.currentUser);
  }


  differenceDate(lastDate){
    return this.clientService.differenceDate(lastDate);
  }

  getLastVisit(SBTXarrayVisits, currentUser){
    return this.clientService.getLastVisit(SBTXarrayVisits, currentUser);
  }

}
