import { Route, RouterModule } from '@angular/router';
import { ClientsListComponent } from './client-container/clients-list/clients-list.component';
import { ClientDetailsComponent } from './client-container/client-details/client-details.component';
import { ClientEditComponent } from './client-container/client-edit/client-edit.component';
// import { HomepageComponent } from './auth/homepage/homepage.component';
import { SignupComponent } from './auth/signup/signup.component';
import { SigninComponent } from './auth/signin/signin.component';
import { AuthGuard } from './shared/guards/auth.guard';
import { AdminGuard } from './shared/guards/admin.guard';
import { ProfileComponent } from './profile/profile.component';
import { UsersListComponent } from './user-container/users-list/users-list.component';
import { UsersDetailsComponent } from './user-container/users-details/users-details.component';
import { UsersCreateComponent } from './user-container/users-create/users-create.component';
import { SelectionListComponent } from './selection-container/selection-list/selection-list.component';

const APP_ROUTE: Route[] = [
    { path: '', component: SigninComponent},
    // { path: '', redirectTo: 'listingClients', pathMatch: 'full'},
    { path: 'signin', component: SigninComponent},    
    { path: 'listingClients', canActivate: [AuthGuard], component: ClientsListComponent },
    { path: 'ficheClient/:id', canActivate: [AuthGuard], component: ClientDetailsComponent },
    { path: 'ficheClient/:id/edit', canActivate: [AuthGuard], component: ClientEditComponent },
    { path: 'new', canActivate: [AuthGuard], component: ClientEditComponent },
    { path: 'monCompte', canActivate: [AuthGuard], component: ProfileComponent },
    { path: 'gestionComptes', canActivate: [AuthGuard, AdminGuard], component: UsersListComponent },
    { path: 'NouveauComptes', canActivate: [AuthGuard, AdminGuard], component: UsersCreateComponent },
    { path: 'ficheUser/:id', canActivate: [AuthGuard, AdminGuard], component: UsersDetailsComponent },
    { path: 'selection', canActivate: [AuthGuard, AdminGuard], component: SelectionListComponent },
]
export const AppRouting = RouterModule.forRoot(APP_ROUTE);
