import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Client } from '../../shared/client.model';
import { ClientService } from '../../shared/services/client.service';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { User } from 'src/app/shared/models/user.model';
import { UserService } from 'src/app/shared/services/user.service';
import { Visites } from 'src/app/shared/models/visites.model';
import { filter } from 'rxjs/operators';
import { MyVisitesPipe } from 'src/app/shared/pipes/MyVisitesPipe';



@Component({
  selector: 'app-client-details',
  templateUrl: './client-details.component.html',
  styleUrls: ['./client-details.component.css'],
  providers: [MyVisitesPipe]
})
export class ClientDetailsComponent implements OnInit {
  public client : Client;
  public Id: any;
  public visites : Visites[] = [];
  public currentUser: Observable<User>;
  public open : boolean = false;
  public openValid : boolean = false;

  constructor(private userService: UserService, private router: Router, private activatedRoute : ActivatedRoute , private clientService : ClientService) { }

  ngOnInit() {
    this.activatedRoute.params.subscribe( (params: Params) => {
      if (params.id) {
        this.Id = params.id;
      } else {
        this.Id = 'error';
      };

      this.clientService.getClient(this.Id).subscribe( (client: Client) => {
        this.client = client;
      });
    });


    this.visites = this.client.visitesSBTX;


    this.currentUser = this.userService.currentUser;
    if (this.currentUser == undefined){
      this.userService.getCurrentUser().subscribe();
      this.currentUser = this.userService.currentUser;
    }
  }

  getURLforEdit(): string[] {
    return [ '/ficheClient', this.Id + '', 'edit'];
  }

  ClientDelete(): void {
    const clientId = this.client._id;
    this.clientService.deleteClient(clientId);
  }
  
  // deleteConfirm(){
  //   document.querySelector
  // }

  endOfVisit(): void {
    this.clientService.createVisite(this.Id);
  }

  customerOfIncludes(companyName){
    const array = this.client.customerOf;
    if(array.includes(companyName)){
      return true;
    } else {
      return false;
    }
  }

  differenceDate(lastDate){
    return this.clientService.differenceDate(lastDate);
  }

  getLastVisit(SBTXarrayVisits, currentUser){
    return this.clientService.getLastVisit(SBTXarrayVisits, currentUser);
  }

}
