import { Pipe, PipeTransform } from '@angular/core';
import { Visites } from '../models/visites.model';
import { UserService } from '../services/user.service';
@Pipe({
  name: 'myVisites'
})
export class MyVisitesPipe implements PipeTransform {
  transform(visites: Visites[], currentU): Visites[] | null {
    return visites.filter(visite => (visite.user === currentU )).reverse();
  };
}
