import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { User } from '../../shared/models/user.model';
import { UserService } from '../../shared/services/user.service';
import { UserVisites } from 'src/app/shared/models/userVisites.model';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-users-details',
  templateUrl: './users-details.component.html',
  styleUrls: ['./users-details.component.css']
})
export class UsersDetailsComponent implements OnInit {
  public user : User;
  public index: number;
  public open : boolean = false;
  public userVisites : UserVisites[] =[];
  public currentUser: Observable<User>;


  constructor(private activatedRoute : ActivatedRoute , private userService : UserService, private router: Router) { }

  ngOnInit() {
    this.currentUser = this.userService.currentUser;
    this.activatedRoute.params.subscribe( (params: Params) => {
      this.index = params.id;
      this.user = this.userService.getUser(params.id);
    });
    if (this.user.myUserVisite) {
      this.userVisites = this.user.myUserVisite;
    };
  }

  getURL(): string[] {
    return [ '/ficheUser', this.index + '', 'edit'];
  }

  userDelete(){
    const userId = this.user._id;
    this.userService.deleteUser(userId);
    this.userService.initUsers();
    this.router.navigate(['gestionComptes']);
  }

}
