import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { User } from '../models/user.model';
import { HttpClient } from '@angular/common/http';
import { tap, switchMap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  public currentUser: BehaviorSubject<User> = new BehaviorSubject(null);
  public users:  BehaviorSubject<User[]> = new BehaviorSubject(null) 
  
  constructor(private http: HttpClient) {
    this.initUsers();
    // this.getCurrentUser().subscribe();
  }

  initUsers(): void {
    this.http.get('/api/user/list').subscribe( (Users: User[]) => {
      this.users.next(Users);
    });
  }

  getUser(index: number): User{
    return this.users.value[index];
  }

  public getCurrentUser(): Observable<User> {
    if (this.currentUser.value) {
      return this.currentUser;
    } else {
      return this.http.get<User>('/api/user/current').pipe(
        tap( (user: User) => {
          this.currentUser.next(user);
        }),
        switchMap( () => {
          return this.currentUser;
        })
      );
    }
  }

  userCreate(user: User): void{
    const userCreated = user;
    const users = this.users.value.slice();
    users.push(new User(
      user.companyName,
      user.admin,
      user.civilite,
      user.lastName,
      user.firstName,
      user.userName,
      user.mail,
      user.password,
      user.myUserVisite
    ));
    this.users.next(users);
    this.http.post('/api/auth/signup', userCreated).subscribe();
  }

  deleteUser(clientID): void{
    this.http.delete('/api/user/delete/'+clientID).subscribe();
    this.initUsers();
  }

  changeUserPassword(formValue, currentUserId, currentUserPassword): void {
    const body = {
      formValue : formValue,
      currentUserId : currentUserId,
      currentUserPassword : currentUserPassword
    }
    this.http.put('/api/user/change-pass/'+currentUserId, body).subscribe();
  }

}
