import { Component, OnInit, OnDestroy } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { JwtToken } from '../../models/jwt-token.model';
import { Subscription, Observable } from 'rxjs';
import { UserService } from '../../services/user.service';
import { User } from '../../models/user.model';

@Component({
  selector: 'app-home-page-top-bar',
  templateUrl: './home-page-top-bar.component.html',
  styleUrls: ['./home-page-top-bar.component.css']
})
export class HomePageTopBarComponent implements OnInit, OnDestroy {
  public jwtToken: JwtToken;
  public subscription: Subscription;
  public currentUser: Observable<User>;

  constructor(private authService: AuthService, private userService: UserService) { }

  ngOnInit() {
    this.subscription = this.authService.jwtToken.subscribe( (jwtToken: JwtToken) => {
      this.jwtToken = jwtToken;
    });
    if (!this.currentUser) {
      this.userService.getCurrentUser().subscribe();
      this.currentUser = this.userService.currentUser;
    };
  }

  public logout(): void{
    this.authService.logout();
  }

  ngOnDestroy() {
    if (this.subscription) { this.subscription.unsubscribe();}
  }

}
