import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Client } from 'src/app/shared/client.model';
import { ClientService } from 'src/app/shared/services/client.service';
import { User } from 'src/app/shared/models/user.model';
import { UserService } from 'src/app/shared/services/user.service';
import { SelectionService } from 'src/app/shared/services/selection.service';
import { FilterPipe } from 'src/app/shared/pipes/filter.pipe';

@Component({
  selector: 'app-selection-list',
  templateUrl: './selection-list.component.html',
  styleUrls: ['./selection-list.component.css'],
  providers: [FilterPipe]
})
export class SelectionListComponent implements OnInit {
  search: string = '';
  public clients: Client[];
  public currentUser: Observable<User>;
  public selectAll: boolean = false;



  constructor(private clientService : ClientService, private userService: UserService, private selectionService: SelectionService) { }
  
  ngOnInit() {
    this.userService.getCurrentUser().subscribe();
    this.currentUser = this.userService.currentUser;

    this.clientService.clients.subscribe( (clients: Client[]) => {
      this.clients = clients;
    });
  }



  switchSelection(client_id, isSelected, clientMail){
    this.clientService.switchSelection(client_id, isSelected);
    this.selectionService.addToLists(!isSelected, client_id, clientMail);
  }

  switchSelectAll(){
		const clients = this.clientService.clients.value.slice();
    const newSelectAllValue = !this.selectAll;
    this.selectAll = newSelectAllValue;

    const allMail = clients.map( c => c.mail );
    const allId = clients.map( c => c._id );



    if(this.selectAll){
      this.clientService.selectionSwitchAll(true);
      this.selectionService.addAllList(true, allMail, allId);
    } else {
      this.clientService.selectionSwitchAll(false);
      this.selectionService.addAllList(false);
    }
  }

  isEmpty(): boolean{
    if(this.selectionService.mailSelectedList.length != 0){
      return true;
    } else {
      return false;
    }
  }


  // début de si on veut réinitialiser quand on quite la selection
  // ngOnDestroy() {
  //   this.selectionService.mailSelectedList =[];
  //   this.selectionService.deleteSelectedList = [];
  // }

  deleteThatList(){
    this.clientService.deleteMultipleClient(this.selectionService.deleteSelectedList);
  }


  sendMailList(userMail){
    console.log(userMail);
    this.selectionService.sendMailList(userMail);
  }


}

