import { Injectable } from '@angular/core';
import { Client } from '../client.model';
import { BehaviorSubject, Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { filter, map } from 'rxjs/operators';
import { UserService } from './user.service';
import { Router } from '@angular/router';
import { SelectionService } from './selection.service';
import * as moment from 'moment';


@Injectable({
  providedIn: 'root'
})

export class ClientService {
  public clients:  BehaviorSubject<Client[]> = new BehaviorSubject(null);


  constructor(private selectionService: SelectionService,  private router: Router, private http: HttpClient, private userService: UserService) {
    this.initClients();
  }

  initClients(): void {
    this.http.get('/api/clients/list').subscribe( (clients: Client[]) => {
      this.clients.next(clients);
    });
  }
  
  getClient(Id: any): Observable<Client>{
    if (this.clients.value){
      const clients = this.clients.value.slice();
      const index = clients.map( c => c._id ).indexOf(Id);
      return this.clients.pipe(
        filter( (clients: Client[]) => clients != null ), 
        map( (clients: Client[]) => clients[index] )
      );
    }else{
      // Quelque chose a faire ici
      console.log('Crash :)');
      this.router.navigate(['listingClients']);
    };
  }

  deleteClient(clientID): void{
    const clients = this.clients.value.slice();
    const index = clients.map( c => c._id ).indexOf(clientID);
    clients.splice(index,1);

    this.http.delete('/api/clients/delete/'+clientID, {observe: 'response'}).subscribe(resp => {
      if(resp.status == 200){
        this.router.navigate(['listingClients']);
        this.clients.next(clients);
      } else {
        alert('Désolé erreur, vérifiez que l\'action est bien passée ou recommencez. Si le problème continue, merci de faire remonter l\'info');
      }
    });
  }

  addClient(client: Client): void{
    const clientCreated = client;
    this.http.post('/api/clients/create', clientCreated).subscribe(newClientId => {
      
      const clients = this.clients.value.slice();
      clients.push(new Client(
        client.selection,
        client.customerOf,
        client.civilite, 
        client.lastName, 
        client.firstName, 
        client.post, 
        client.companyName,  
        client.companyAdress, 
        client.companyPostCode, 
        client.companyCity, 
        client.companyType, 
        client.cellPhone, 
        client.phone, 
        client.mail, 
        client.companyLogo,
        client.infos,
        client.visitesSBTX,
        newClientId 
      ));
      this.clients.next(clients);
      this.router.navigate(['ficheClient/' + newClientId.toString()]);
    });
  }



  editClient(editClient: Client, client_ID): void{
    const clients = this.clients.value.slice();
    const index = clients.map( c => c._id ).indexOf(editClient._id);
    clients[index] = editClient;
    this.clients.next(clients);
    this.save(client_ID, editClient);
  }

  save(client_ID, editClient): void {
    this.http.put('/api/clients/edit/'+client_ID, editClient).subscribe();
  }

  createVisite(client_ID): void{
    let date = this.getDate();

    const currentUser = this.userService.currentUser
    // subscribe current User ?

    const clients = this.clients.value.slice();
    const users = this.userService.users.value.slice();

    const username = currentUser.value.userName;
    const companyName = currentUser.value.companyName;
    const myPush = {user: username, company: companyName ,date: date};

    const clientIndex = clients.map( c => c._id ).indexOf(client_ID);
    const Cvisited = clients[clientIndex];
    
    const userIndex = users.map( c => c.userName ).indexOf(username);
    const userWhoVisit =  users[userIndex]

    const personVisited = Cvisited.civilite+' '+Cvisited.firstName+' '+Cvisited.lastName;
    const companyNameVisited = Cvisited.companyName;
    const myUserVisitePush = {
      personVisited: personVisited,
      companyNameVisited: companyNameVisited,
      company: companyName,
      date: date
    };

    // sauvegarde Front
    Cvisited.visitesSBTX.push(myPush);
    userWhoVisit.myUserVisite.push(myUserVisitePush);
    
    
    this.userService.users.next(users);
    this.clients.next(clients);
    // sauvegarde Back
    this.http.put('/api/clients/visite/'+client_ID , myPush).subscribe();
    this.http.put('/api/user/visite/'+currentUser.value._id , myUserVisitePush).subscribe();
  }
  
  getDate(): string{
    let currentDate = new Date;
    let date = currentDate.getDate();
    let month = currentDate.getMonth(); //Be careful! January is 0 not 1
    let year = currentDate.getFullYear();
    
    function pad(n) {
      return n<10 ? '0'+n : n;
    }
    let ddmmyyyy = pad(date) + "-" + pad(month + 1) + "-" + year;
    return ddmmyyyy;
  }

  postAvatar(fileToUpload: File, clientId, client) {
    const endpoint = '/api/clients/avatar/'+clientId;
    const formData: FormData = new FormData();

    const clients = this.clients.value.slice();
    const index = clients.map( c => c._id ).indexOf(clientId);

    
    formData.append('avatar', fileToUpload, fileToUpload.name);

    this.http.post(endpoint, formData).subscribe(newLogoPath => {
      clients[index] = new Client(
        client.selection,
        client.customerOf,
        client.civilite, 
        client.lastName, 
        client.firstName, 
        client.post, 
        client.companyName,  
        client.companyAdress, 
        client.companyPostCode, 
        client.companyCity, 
        client.companyType, 
        client.cellPhone, 
        client.phone, 
        client.mail, 
        newLogoPath+'',
        client.infos,
        client.visitesSBTX,
        client._id
      );
      this.clients.next(clients);
    });
    // return;
  }

  customerOf(value: Boolean, client, currentUserCompanyName){
    const client_ID = client._id;
    const clients = this.clients.value.slice();
    const index = clients.map( c => c._id ).indexOf(client._id);
    if(value === true){
      client.customerOf.push(currentUserCompanyName);
      clients[index] = client;
      this.http.put('/api/clients/customerof/'+client_ID , client).subscribe();
      this.clients.next(clients);
    } else if (value === false){
      const indexCompany = client.customerOf.indexOf(currentUserCompanyName);
      client.customerOf.splice(indexCompany,1);
      clients[index] = client;
      this.http.put('/api/clients/customerof/'+client_ID , client).subscribe();
      this.clients.next(clients);
    } else {
      console.log('what\'s wrong with you ?');
    }
  }


  switchSelection(clientId, isSelected) : void {
		const clients = this.clients.value.slice();
    const selected = !isSelected;
    const index = clients.map( c => c._id ).indexOf(clientId);

		clients[index].selection = selected;
		this.clients.next(clients);
	}

  selectionSwitchAll(boolean){
    const clients = this.clients.value.slice();
    clients.map( c => c.selection = boolean );
		this.clients.next(clients);
  }


  deleteMultipleClient(clientIDlist): void{
    const clients = this.clients.value.slice();
    
    for (let index = 0; index < clientIDlist.length; index++) {
      const element = clientIDlist[index];
      const clientIndex = clients.map( c => c._id ).indexOf(element);
      clients.splice(clientIndex,1);
    }
    this.http.put('/api/clients/deleteMultiple', clientIDlist, {observe: 'response'}).subscribe(resp => {
      this.selectionService.mailSelectedList = [];
      this.selectionService.deleteSelectedList = [];
      if(resp.status == 200){
        this.clients.next(clients);
      } else {
        alert('Désolé erreur, vérifiez que l\'action est bien passée ou recommencez. Si le problème continue, merci de faire remonter l\'info');
      }
    });
  }

  differenceDate(lastDate){
    const lastVisit = moment(lastDate, "DD-MM-YYYY");
    const now = moment();
    const diff = now.diff(lastVisit, 'days');

    if(diff > 180){
      return false;
    } else {
      return true
    };     
  }

  getLastVisit(SBTXarrayVisits, currentUser){
    const newArray = SBTXarrayVisits.filter(visite => (visite.user === currentUser));
    const index = newArray.length -1;
    if (newArray.length != 0){
      return newArray[index].date;
    } else {
      return '01-01-2019'
    };
  }

}

