import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { ClientService } from './client.service';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class SelectionService {
  public mailSelectedList : string[] = [];
  public deleteSelectedList : string[] = [];
  
  
  constructor(private http: HttpClient) { }
  

  addToLists(isSelected, clientId, clientMail):void{
    // const mailArray = this.mailSelectedList.value.splice() 
    if(isSelected){
      this.mailSelectedList.push(clientMail);
      this.deleteSelectedList.push(clientId);
    }else{
      const indexMail = this.mailSelectedList.indexOf(clientMail);
      this.mailSelectedList.splice(indexMail, 1);

      const indexId = this.deleteSelectedList.indexOf(clientId);
      this.deleteSelectedList.splice(indexId, 1);
    }
    console.log(this.mailSelectedList);
    console.log(this.deleteSelectedList);
  }

  addAllList(boolean, mailList?, IdList?){
    if(boolean === true){
      this.mailSelectedList = mailList;
      this.deleteSelectedList = IdList;
    }else{
      this.mailSelectedList = [];
      this.deleteSelectedList = [];
    }
    console.log(this.mailSelectedList);
    console.log(this.deleteSelectedList);
  }
  
  sendMailList(userMail){
    const body = {
      userMail : userMail,
      mailList : this.mailSelectedList
    }
    this.http.post('/send/mail', body).subscribe();
  }


}